'use client';
import { useEffect } from 'react';
import Link from 'next/link';
import { useDispatch, useSelector } from 'react-redux';
import { useRouter } from 'next/navigation';
import { setSession } from '@/app/_lib/sessionReducer';
import { useSession, signOut } from 'next-auth/react';
import Cookies from 'js-cookie';
import { fetchData } from '@/app/_lib/userReducer';
import { Button } from "@/components/ui/button";
import { DropdownMenu, DropdownMenuContent, DropdownMenuTrigger, DropdownMenuItem } from "@/components/ui/dropdown-menu";
import { Avatar, AvatarImage, AvatarFallback } from "@/components/ui/avatar";
import { Sheet, SheetTrigger, SheetContent } from "@/components/ui/sheet";
import { Input } from "@/components/ui/input";
import { ShoppingCartIcon } from "lucide-react";

export default function UserMenu() {
  const dispatch = useDispatch();
  const router = useRouter();
  const { data: session } = useSession();
  const userData = useSelector((state) => state.userData?.user);

  useEffect(() => {
    const userIdFromCookie = JSON.parse(Cookies.get("shopflow_session") || null);
    const userId = userIdFromCookie?.user?.id;
    const token = userIdFromCookie?.token;

    if (userId) {
      dispatch(setSession(userIdFromCookie));
      dispatch(fetchData(`user/userProfileInfo?userId=${userId}`, token));
    }
  }, [dispatch]);

  const handleSignOut = async () => {
    try {
      await signOut({ callbackUrl: '/' });
      Cookies.remove("shopflow_session");
      dispatch(setSession(null));
      router.refresh();
    } catch (error) {
      console.error('Sign-out error:', error);
    }
  };

  return (
    <nav className="bg-gray-900 px-4 lg:px-6 py-2">
      <div className="flex justify-between items-center mx-auto max-w-screen-xl text-white">
        <Link href="/" className="flex">
          <span className="text-md sm:text-md md:text-3xl font-bold">
            <span className="text-white">Shop</span>
            <span className="text-orange-500">Flow</span>
            <br />
            <span className="block text-xs font-light">Global leading online shop</span>
          </span>
        </Link>
        <Input type="text" placeholder="Search products..." className="w-64 hidden lg:block" />
        <div className="hidden lg:flex items-center space-x-6">
          <UserMenu />
          <Sheet>
            <SheetTrigger asChild>
              <Button variant="ghost">
                <ShoppingCartIcon className="text-white" />
              </Button>
            </SheetTrigger>
            <SheetContent>
              <p className="text-center text-gray-700">Your cart is empty</p>
            </SheetContent>
          </Sheet>
        </div>
      </div>
    </nav>
  );
}
